import debounce from 'debounce';
import { defineComponent } from 'vue';
import FolderMultiple from 'vue-material-design-icons/FolderMultiple.vue';
import Plus from 'vue-material-design-icons/Plus.vue';
import { NcButton } from '@nextcloud/vue';
import { getFilePickerBuilder } from '@nextcloud/dialogs';
import { translate as t } from '@nextcloud/l10n';
import PhotosFolder from './PhotosFolder.vue';
export default defineComponent({
  name: 'PhotosSourceLocationsSettings',
  components: {
    NcButton,
    PhotosFolder,
    Plus
  },
  data() {
    return {
      FolderMultiple
    };
  },
  computed: {
    /** @return {string[]} */
    photosSourceFolders() {
      return this.$store.state.userConfig.photosSourceFolders;
    }
  },
  methods: {
    debounceAddSourceFolder: debounce(function () {
      this.addSourceFolder(...arguments);
    }, 200, false),
    async openFilePicker(title) {
      const picker = getFilePickerBuilder(title).setMultiSelect(false).setModal(true).setType(1).addMimeTypeFilter('httpd/unix-directory').allowDirectories().build();
      return picker.pick();
    },
    async addSourceFolder() {
      const pickedFolder = await this.openFilePicker(t('photos', 'Select a source folder for your media'));
      if (this.photosSourceFolders.includes(pickedFolder)) {
        return;
      }
      this.$store.dispatch('updateUserConfig', {
        key: 'photosSourceFolders',
        value: [...this.photosSourceFolders, pickedFolder]
      });
    },
    removeSourceFolder(index) {
      const folders = [...this.photosSourceFolders];
      folders.splice(index, 1);
      this.$store.dispatch('updateUserConfig', {
        key: 'photosSourceFolders',
        value: folders
      });
    },
    t
  }
});