import { defineComponent } from 'vue';
import Folder from 'vue-material-design-icons/Folder.vue';
import Close from 'vue-material-design-icons/Close.vue';
import { NcButton } from '@nextcloud/vue';
import { translate as t } from '@nextcloud/l10n';
export default defineComponent({
  name: 'PhotosFolder',
  components: {
    NcButton,
    Folder,
    Close
  },
  props: {
    path: {
      type: String,
      required: true
    },
    canDelete: {
      type: Boolean,
      default: false
    },
    rootFolderLabel: {
      type: String,
      required: true
    },
    rootFolderIcon: {
      type: Object,
      required: true
    }
  },
  emits: ['remove-folder'],
  computed: {
    folderName() {
      if (this.path === '/') {
        return this.rootFolderLabel;
      } else {
        return this.path.split('/').pop();
      }
    },
    /**
     * Return the summary path of the folder
     * Examples:
     *  - /        ==> Home
     *  - /a       ==> nothing
     *  - /a/b     ==> /a
     *  - /a/b/c   ==> /a/b
     *  - /a/b/c/d ==> /a/b
     */
    subname() {
      const slashesCount = (this.path.match(/\//g) ?? []).length;
      switch (slashesCount) {
        case 1:
          return '';
        case 2:
          return this.path.split('/').splice(0, 2).join('/');
        default:
          return this.path.split('/').splice(0, 3).join('/');
      }
    }
  },
  methods: {
    emitRemoveSourceFolder() {
      this.$emit('remove-folder');
    },
    t
  }
});